@import "colors";

.founder{
    margin-top: 50px;
    width: 100%;
    height: 80vh;
    background-color: $red;

    > div {
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: white;
        width: 50%;
        text-align: center;
        margin: auto;

        > img {
            border-radius: 100%;
        }
    
        > h3 {
            font-size: 1.3rem;
            padding: 2rem 0 1rem 0;
        }
    
        > p {
            letter-spacing: 3px;
            font-size: 0.8rem;
            font-family: cursive;
            line-height: 200%;
        }
    }

   

}
